/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  width: 36px;
  height: 30px;
  right: 15px;
  top: 10px;
  color: #ffffffb8;
  background: #000000c0;
  border-radius: 50%;
  padding: 10px;
  -webkit-box-shadow: 0 10px 10px 0 rgba(10, 10, 10, 0.137), 0 10px 10px 0 rgba(0, 0, 0, 0.144);
          box-shadow: 0 10px 10px 0 rgba(10, 10, 10, 0.137), 0 10px 10px 0 rgba(0, 0, 0, 0.144);
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  z-index: 5 !important;
}

.bm-burger-button:hover{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #e4e4e4;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background:rgba(34, 34, 34, 0.99);
  padding: 2.2em 0.6em 0;
  font-size: 1.15em;
  overflow-y: hidden !important;
  text-align: center;
}

.bm-menu-wrap {
    z-index: 6 !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0em;
  overflow-y: hidden;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.61);
}

